import './AdminFabricanteDetail.css'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import '@uppy/image-editor/dist/style.css'

import { Delete, Star, Visibility } from '@material-ui/icons'
import Uppy from '@uppy/core'
import { DashboardModal } from '@uppy/react'
import Webcam from '@uppy/webcam'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemDetail, Page } from '../../components'
import LoaderAdmin from '../../components/LoaderAdmin'
import { RUTAS, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { DetailViewProps, useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { ImagenProducto, Producto } from '../../services/api'

const ImageCompressor = require('uppy-plugin-image-compressor')

const ImageEditor = require('@uppy/image-editor')
const Espanol = require('@uppy/locales/lib/es_ES')

const XHRUpload = require('@uppy/xhr-upload')

export const AdminProductoDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Producto, setProducto] = useState(null as any)
  const [Visible, setVisible] = useState(false)
  const [Imagenes, setImagenes] = useState<ImagenProducto[]>([])
  const [OpcionesImagenShow, setOpcionesImagenShow] = useState(0)
  const Estado = match.params ? (match.params as any).estado : 'moderado'
  const ProductoId = match.params.id
  const apiToast = useApiToaster()

  const setup = async () => {
    obtenerProducto()
  }

  useEffect(() => {
    setup()
  }, [ProductoId])

  const obtenerProducto = () => {
    setLoading(true)
    rootStore.environment.api.getProductoAdmin(ProductoId).then((result) => {
      if (result.kind === 'ok') {
        setProducto(result.data)
        setImagenes(result.data.imagenes)
      }
      setLoading(false)
    })
  }

  const uppy = React.useMemo(() => {
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*'] },
      meta: {
        producto: ProductoId,
      },
      locale: Espanol,
    })
      .use(Webcam)
      .use(XHRUpload, {
        endpoint: rootStore.environment.api.config.url + '/imagen_producto/',
        headers: {
          Authorization: 'Token ' + loginStore.token,
        },
        method: 'POST',
        formData: true,
        fieldName: 'imagen',
      })
      .use(ImageEditor, {
        id: 'ImageEditor',
        quality: 0.8,
        cropperOptions: {
          viewMode: 1,
          background: false,
          autoCropArea: 1,
          responsive: true,
        },
      })
      .use(ImageCompressor, {
        maxHeight: 2000,
        maxWidth: 2000,
        quality: 0.7,
      })
  }, [])

  useEffect(() => {
    return () => uppy.close()
  }, [])

  uppy.on('complete', (result: any) => {
    const imagenesSubidas: { id: any; imagen: any }[] = []
    for (let i = 0; i < result.successful.length; i++) {
      // @ts-ignore
      let data = result.successful[i].response.body
      let imagen = {
        // @ts-ignore
        id: data.id,
        // @ts-ignore
        imagen: data.imagen,
        // @ts-ignore
        producto: data.producto,
        resoluciones: {
          thumbnail: data.imagen,
          medium: data.imagen,
          fullres: data.imagen,
        },
      }
      if (!imagenesSubidas.includes(imagen)) {
        imagenesSubidas.push(imagen)
      }
    }
    let imagenesUpdate: any[] = []
    imagenesUpdate = imagenesUpdate.concat(Imagenes)
    imagenesUpdate = imagenesUpdate.concat(imagenesSubidas)
    setImagenes(imagenesUpdate)
  })

  const aprobarProducto = () => {
    if (!Producto || Producto.activo || !loginStore.administrador) {
      return
    }
    const datos = { id: Producto.id, activo: true }
    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      apiToast(result, 'El producto se aprobó exitosamente!', 'aprobar el producto')
      if (result.kind === 'ok') {
        setProducto(result.response)
      }
    })
  }

  const editar = () => {
    history.push(RUTAS.ADMIN_PRODUCTO_UPDATE(Estado, ProductoId))
  }

  const eliminar = () => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarProducto(ProductoId).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Eliminado satisfactoriamente', 'al eliminar')
      if (result.kind === 'ok') {
        history.push('/admin/producto/' + Estado)
      }
    })
  }

  const eliminarImagenProducto = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarImagen(row.id).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Imagen eliminada satisfactoriamente', 'al eliminar')
      if (result.kind === 'ok') {
        const imagenesProducto = [...Imagenes]
        const imagenEliminar = imagenesProducto.find((imagen) => imagen.id === row.id)
        if (imagenEliminar) {
          imagenesProducto.splice(imagenesProducto.indexOf(imagenEliminar), 1)
          setImagenes(imagenesProducto)
        }
      }
    })
  }

  const seleccionarImagenPortada = (imagen_id: number) => {
    rootStore.environment.api.seleccionarImagenPortada(imagen_id).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Imagen de portada seleccionada satisfactoriamente', 'eliminar')
      if (result.kind === 'ok') {
        const imagenes = [...Imagenes]
        const imagenesProducto = imagenes.map((img: any) => ({ ...img, portada: false }))
        const imagenPortada = imagenesProducto.find((imagen) => imagen.id === imagen_id)
        const imagenPortadaSeleccionada = { ...imagenPortada, portada: true }
        if (imagenPortada) {
          imagenesProducto.splice(imagenesProducto.indexOf(imagenPortada), 1)
          imagenesProducto.unshift(imagenPortadaSeleccionada)
          setImagenes(imagenesProducto)
        }
      }
    })
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading} />
      {Producto && !Loading && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <DashboardModal
            uppy={uppy}
            closeModalOnClickOutside
            open={Visible}
            onRequestClose={() => {
              setVisible(false)
              uppy.reset()
            }}
            plugins={['Webcam', 'ImageEditor']}
            metaFields={[{ id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo' }]}
          />
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">{Producto.nombre}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/producto/' + Estado)}>Productos</a>
                  </li>
                  <li className="breadcrumb-item active">{Producto.nombre}</li>
                </ol>
              </div>
            </div>
            <div className={'mb-2 text-right'}>
              {loginStore.administrador && !Producto.activo && (
                <button className={'btn btn-primary ml-3'} title={'Aprobar'} onClick={aprobarProducto}>
                  <FIcon.Check size={14} /> Aprobar
                </button>
              )}
              <button className={'btn btn-primary ml-3'} title={'Editar'} onClick={() => editar()}>
                <FIcon.Edit size={14} /> Editar
              </button>
              <button className={'btn btn-primary ml-3'} title={'Eliminar'} onClick={() => eliminar()}>
                <FIcon.Trash size={14} /> Eliminar
              </button>
            </div>
            <div className="card">
              <div className="card-body">
                <div className={'row'}>
                  <AdminItemDetail titulo={'Nombre'} texto={Producto.nombre} columnas={4} />
                  <AdminItemDetail titulo={'Descripción corta'} texto={Producto.descripcion} columnas={8} />
                  <AdminItemDetail titulo={'Descripción extensa'} html={Producto.descripcion_extensa} columnas={12} />
                  <AdminItemDetail titulo={'Especificaciones'} html={Producto.especificaciones} columnas={12} />
                  <AdminItemDetail titulo={'Etiquetas'} texto={Producto.etiquetas.replace(',', ', ')} columnas={3} />
                  <AdminItemDetail titulo={'Categoría'} texto={Producto.categoria.nombre} columnas={3} />
                  <AdminItemDetail
                    titulo={'Refrigeración'}
                    texto={
                      TIPOS_REFRIGERACION_SELECT.filter((val) => parseInt(val.value) === Producto.refrigeracion)[0]
                        .label
                    }
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Estado'}
                    texto={Producto.activo ? 'Activo' : 'Pendiente de moderación'}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemDetail titulo={'Costo minorista'} separador_dinero texto={Producto.precio} columnas={3} />
                  <AdminItemDetail
                    titulo={'Costo mayorista'}
                    separador_dinero
                    texto={Producto.precio_mayorista}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Cantidad disponible'}
                    separador_miles
                    texto={Producto.cantidad_disponible}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Cantidad mayorista'}
                    separador_miles
                    texto={Producto.cantidad_mayorista}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemDetail titulo={'Peso kg'} texto={Producto.peso_kg} columnas={3} />
                  <AdminItemDetail titulo={'Altura cm'} texto={Producto.alto_cm} columnas={3} />
                  <AdminItemDetail titulo={'Ancho cm'} texto={Producto.ancho_cm} columnas={3} />
                  <AdminItemDetail titulo={'Profundidad cm'} texto={Producto.profundidad_cm} columnas={3} />
                  <div className={'col-12 col-lg-3'}>
                    <strong>Video</strong>
                    {!Producto.video && <p>No posee video</p>}
                    {Producto.video && (
                      <video src={Producto.video} controls style={{ width: 640, height: 360, maxWidth: '100%' }} />
                    )}
                  </div>
                </div>
              </div>
            </div>
            <h4>Imágenes del producto</h4>
            <div className="card">
              <div className="card-body">
                <div className={'mb-2 text-right'}>
                  <button className={'btn btn-primary'} title={'Agregar imágenes'} onClick={() => setVisible(!Visible)}>
                    <FIcon.Plus size={14} /> Agregar imágenes
                  </button>
                </div>
                <div className={'row'}>
                  {Imagenes &&
                    Imagenes.map((imagen: any) => (
                      <div
                        key={imagen.id}
                        className={'col-3 text-center'}
                        onMouseEnter={() => setOpcionesImagenShow(imagen.id)}
                        onMouseLeave={() => setOpcionesImagenShow(0)}
                      >
                        {OpcionesImagenShow === imagen.id && (
                          <div style={{ position: 'absolute', right: 0 }}>
                            <div className={'btn-group'}>
                              <button
                                className={'btn btn-primary'}
                                title={'Seleccionar como foto de portada'}
                                onClick={() => seleccionarImagenPortada(imagen.id)}
                              >
                                {imagen.portada ? <Star style={{ color: 'rgb(252, 76, 2)' }} /> : <Star />}
                              </button>
                              <a
                                className={'btn btn-primary'}
                                title={'Ver imagen del producto'}
                                target="_blank"
                                href={imagen.imagen}
                              >
                                <Visibility />
                              </a>
                              <button
                                className={'btn btn-primary'}
                                title={'Eliminar producto'}
                                onClick={() => eliminarImagenProducto(imagen)}
                              >
                                <Delete />
                              </button>
                            </div>
                          </div>
                        )}
                        <img
                          // @ts-ignore
                          src={imagen.resoluciones.medium}
                          style={{ maxHeight: 250, maxWidth: 250 }}
                        />
                      </div>
                    ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminProductoDetail
